import React from 'react';

import {Breadcrumb} from 'antd';

import './index.scss';

export default ({routes, params}) => {
    return (
        <div className="bread-crumb">
            <span className="fl"> : &nbsp; &nbsp;</span> 
            <div className="fl">
                <Breadcrumb routes={routes} params={params}/>
            </div>
        </div>
    )
}


